import React, { Component } from 'react'
import { Card, Row, Col, Container } from "react-bootstrap";


 class ShowPage extends Component {

    movie = this.props.location.movie;

    render() {
        console.log(this.movie);
        return (

        <Card>
            <Card.Img variant="top" src={`https://image.tmdb.org/t/p/w500${this.movie.poster_path}`}/>
            <Card.Body>
        <Card.Title>{this.movie.title}</Card.Title>
        <Card.Text>{this.movie.overview}</Card.Text>
                <Card.Text>{this.movie.vote_average}</Card.Text>
            </Card.Body>
        </Card>
        )
    }


}
export default ShowPage;